/*-------------------- global-styles --------------------*/
html {
	min-width: 320px;
	background: #fff;
}
body {
	min-width: $large-width;
	width: 100%;
	height: 100%;
	color: #000;
	font-family: arial, sans-serif;
	font-size: 16px;
	@include large {
		min-width: 1280px;
	}
}
::selection {
	color: #fff;
	background: $base-color;
}
input:-webkit-autofill {
	box-shadow: 0 0 0 30px #fff inset;
}
a {
	color: #000;
	text-decoration: none;
}
.global-wrap {
	display: flex;
	min-height: 100vh;
	flex-direction: column;
	@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) { //fix for ie 10-11
		height: 100%;
	}
	.main {
		flex: 1 0 auto; /* NOT flex: 1; */
	}
}
.wrap {
	position: relative;
	overflow: hidden;
	width: $large-width;
	margin: 0 auto;
	padding: 0 10px;
	@include large {
		width: 100%;
	}
}
.btn {
	position: relative;
	display: inline-block;
	vertical-align: top;
	min-width: 170px;
	text-align: center;
	color: #fff;
	font-size: 18px;
	line-height: 1.15;
	font-weight: 900;
	letter-spacing: 0.36px;
	padding: 9px 20px;
	background: $base-color;
	border: 1px solid $base-color;
	transition: all .3s;
	&:hover {
		color: $base-color;
		background: transparent;
	}
	&.reverse {
		color: $base-color;
		background: transparent;
		&:hover {
			color: #fff;
			background: $base-color;
		}
	}
	&.loading,
	&.added,
	&.error {
		padding-right: 45px;
		&:after {
			content: '';
			position: absolute;
			top: 0;
			bottom: 0;
			right: 20px;
			margin: auto;
			width: 16px;
			height: 16px;
			background-size: contain;
		}
	}
	&.loading {
		&:after {
			background: url('../img/svg-icons/loading.svg') no-repeat center;
			background-size: contain;
		}
	}
	&.added {
		&:after {
			background: url('../img/svg-icons/checked.svg') no-repeat center;
			background-size: contain;
		}
	}
	&.error {
		&:after {
			background: url('../img/svg-icons/error.svg') no-repeat center;
			background-size: contain;
		}
	}
}

