
/*First section*/
.first-section{
    height: calc(100vh + 1000px);
    .text-container{
        position: absolute;
        top: 20vh;
        left: 50%;
        transform: translate(-50%, 0);
        text-align: center;
        width: 800px;
        margin: 0 auto;
        z-index: 1;
        h1{
            font-size: 32px;
            line-height: 1.25;
            font-weight: 700;
            letter-spacing: 0em;
            font-family: "Helvetica Neue","Helvetica","Arial",sans-serif;
        }
        .typography-hero-eyebrow{
            font-size: 110px;
            line-height: .9;
            font-weight: 700;
            letter-spacing: -.009em;
            font-family: "Helvetica Neue","Helvetica","Arial",sans-serif;
            overflow: visible;
            margin-top: 8px;
            height: 220px;
        }
    }
    .sticky-container{
        position: sticky;
        top: 0;
        width: 980px;
        margin: 0 auto;
        .img-container{
            padding: 10vh 0 0 0;
            width: calc(100% + 200px);
            margin: 0 -100px;
            img{
                width: 100%;
            }
        }
        .typography-section-intro{
            margin: 100px 0;
            color: #888;
            font-size: 36px;
            line-height: 1.16667;
            font-weight: 600;
            letter-spacing: 0em;
            font-family: "Helvetica Neue","Helvetica","Arial",sans-serif;
        }
        .typography-intro{
            display: inline;
            color: #000;
            font-size: 22px;
            line-height: 1.18182;
            font-weight: 700;
            margin: 20px 0;
            display: inline-flex;
            align-items: center;
            svg{
                width: 25px;
                height: 25px;
                margin: 0 0 0 5px;
                transition: 0s;
            }
            &:hover{
                color: #06c;
            }
        }
        .typography-intro-cta{
            color: #888;
            font-size: 22px;
            line-height: 1.18182;
            font-weight: 700;
            letter-spacing: .007em;
            font-family: "Helvetica Neue","Helvetica","Arial",sans-serif
        }
    }
}

/*Second section*/
.second-section{
    position: relative;
    padding: 200px 0;
    .features-container{
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;
        position: relative;
        margin: 0 auto;
        width: 980px;
        .typography-features-label{
            width: 34%;
            color: #888;
            font-size: 24px;
            line-height: 1.16667;
            font-weight: 600;
            letter-spacing: .009em;
            font-family: "Helvetica Neue","Helvetica","Arial",sans-serif;
            transform: translate(0, 50px);
            opacity: 0;
            &:nth-last-child(-n+2){
                .badge-line{
                    display: none;
                }
            }
            span{
                font-weight: 700;
                color: #000;
            }
            .badge-line{
                height: 1px;
                width: 0%;
                background-color: #d2d2d7;
                margin: 30px 0;
            }
        }
    }
    .features-hardware{
        position: absolute;
        top: 200px;
        left: 50%;
        height: 660px;
        width: 1440px;
        transform: translate(-50%, 0);
        z-index: -1;
        .start-animation{
            position: absolute;
            bottom: 0;
        }
    }
}

/*Third section*/
.third-section{
    padding: 250px 0 0 0;
    .typography-section-headline{
        width: 980px;
        margin: 0 auto 100px;
        font-size: 70px;
        line-height: 1.05;
        font-weight: 700;
        letter-spacing: -.020em;
        font-family: "Helvetica Neue","Helvetica","Arial",sans-serif;
        span{
            color: #acacac;
        }
    }
    .img-magic-container{
        .first-block-container{
            position: relative;
            width: 100%;
            height: 120vh;
            .first-block{
                height: 100vh;
                width: 100%;
                display: flex;
                justify-content: center;
                .section-content{
                    position: absolute;
                    top: 30%;
                    left: 20vw;
                    transform: translate(0, -50%);
                    opacity: 0;
                    transition: 0.4s;
                    .name-title{
                        font-weight: 700;
                        margin-top: 30px;
                        line-height: 1.3;
                    }
                    .job-title{
                        line-height: 1.3;
                        margin: 5px 0 0 0;
                    }
                }
                .img-background-container{
                    width: 980px;
                    background: #f3f3f3;
                    .img-background{
                        height: 100vh;
                        width: 50%;
                        margin: 0 auto;
                        background: url("../img/third-section/men.jpg") no-repeat;
                        background-size: 100% auto;
                        background-position: 50% 111%; 
                    }
                }
            }
        }
        .second-block{
            position: relative;
            width: 100%;
            .start-first-animation{
                height: 150vh;
            }
            .start-second-animation{
                height: 100vh;   
                overflow: hidden;
                .img-background-container{
                    position: fixed;
                    bottom: 0;
                    left: 0;
                    width: 130vw;
                    height: 0;
                    overflow: hidden;
                    img{
                        width: 100vw;
                        height: 100vh;
                        transform: scale(1.3);
                    }
                }  
                .quote-text{
                    position: fixed;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    font-size: 28px;
                    line-height: 1.14286;
                    font-weight: 600;
                    letter-spacing: 0em;
                    font-family: "Helvetica Neue","Helvetica","Arial",sans-serif;
                    text-align: center;
                    color: #fff;
                    opacity: 0;
                    span{
                        display: block;
                        width: 720px;
                        color: #fff;
                        font-size: 75px;
                        line-height: 1.05;
                        font-weight: 700;
                        letter-spacing: -.015em;
                        font-family: "Helvetica Neue","Helvetica","Arial",sans-serif;
                        line-height: 1.03;
                        text-align: center;
                        margin: 0 0 20px 0;
                        &:before{
                            position: absolute;
                            content: open-quote;
                            left: 50px;
                        }
                        &:after{
                            position: absolute;
                            content: close-quote;
                        }
                    }
                } 
            }
            .typography-section-intro{
                max-width: 980px;
                margin: 0 auto;
                font-size: 36px;
                line-height: 1.16667;
                font-weight: 600;
                letter-spacing: 0em;
                font-family: "Helvetica Neue","Helvetica","Arial",sans-serif;
                color: #888;
                span{
                    color: #000;
                }
            }
        }
    }
}

/*Fourth section*/
.fourth-section{
    &.animated{
        .gallery-screen{
            .right-col{
                img{
                   &:nth-of-type(1){
                    transform: translate(-100%, 0);
                   }
                   &:nth-of-type(2){
                    transform: translate(-80%, 0);
                   }
                   &:nth-of-type(3){
                    transform: translate(-60%, 0);
                   }
                   &:nth-of-type(4){
                    transform: translate(-40%, 0);
                   }
                   &:nth-of-type(5){
                    transform: translate(-20%, 0);
                   }
                }
            }
        }
    }
    .gallery-screen{
        display: flex;
        width: 980px;
        margin: 100px auto;
        background: #f3f3f3;
        .left-col{
            width: 35%;
            padding: 250px 30px 250px 50px;
            display: flex;
            align-items: center;
            font-size: 19px;
            line-height: 1.4211;
            font-weight: 500;
            letter-spacing: .012em;
            font-family: "Helvetica Neue","Helvetica","Arial",sans-serif;
            color: #888;
        }
        .right-col{
            position: relative;
            width: 65%;
            display: flex;
            img{
                position: absolute;
                bottom: 0;
                left: 50%;
                transition: 2s;
               &:nth-of-type(1){
                transform: translate(-100%, 0);
               }
               &:nth-of-type(2){
                transform: translate(-100%, 0);
               }
               &:nth-of-type(3){
                transform: translate(-100%, 0);
               }
               &:nth-of-type(4){
                transform: translate(-100%, 0);
               }
               &:nth-of-type(5){
                transform: translate(-100%, 0);
               }
            }
        }
    }
    .subsection-badges{
        display: flex;
        justify-content: space-between;
        width: 980px;
        margin: 0 auto;
        .col{
            width: 40%;
            .badge-line{
                height: 1px;
                width: 0%;
                background-color: #d2d2d7;
            }
            .badge-content{
                padding: 50px 0;
                opacity: 0;
                span{
                    font-size: 80px;
                    line-height: 1;
                    font-weight: 700;
                    letter-spacing: -.015em;
                    font-family: "Helvetica Neue","Helvetica","Arial",sans-serif;
                }
                p{
                    font-weight: 500;
                    color: #888;
                    font-size: 17px;
                    line-height: 1.23536;
                    font-weight: 400;
                    letter-spacing: -.022em;
                    font-family: "Helvetica Neue","Helvetica","Arial",sans-serif;
                }
            }
        }
    }
    .section-processor{
        max-width: 980px;
        margin: 150px auto;
        h2, h3{
            font-size: 75px;
            font-weight: 700;
            font-size: 80px;
            line-height: 1.05;
            font-weight: 700;
            letter-spacing: -.015em;
            font-family: "SF Pro Display","SF Pro Icons","Helvetica Neue","Helvetica","Arial",sans-serif;
        }
        h3{
            color: #acacac;
        }
    }
}

/*Fifth section*/
.fifth-section{
    .img-magic-container{
        position: relative;
        &.animated{
            .first-img-container{
                .img{
                    display: none;
                }
            }
            .col-second-img-container{
                .second-img-container{
                    position: absolute;
                    top: initial; 
                    bottom: 0;
                    right: 0;
                    width: 100%;
                    .img{
                        background-attachment: initial;
                    }
                }
            }
        }
        .first-img-container{
            height: 100vh;
            .img{
                position: absolute;
                top: 0;
                left: 50%;
                transform: translate(-50%, 0);
                width: 980px;
                height: 100vh;
                margin: 0 auto;
                background: #f3f3f3 url("../img/fifth-section/men.jpg") no-repeat;
                background-size: auto 100%;
                background-position: 50% 0;
                z-index: -1;
            }
            .pro-quote{
                opacity: 0;
                position: fixed;
                top: 50%;
                left: 10%;
                transform: translate(0, -50%);
                width: 315px;
                display: flex;
                flex-direction: column;
                font-weight: 700;
                span{
                    font-size: 48px;
                    line-height: 1.08349;
                    font-weight: 600;
                    letter-spacing: -.003em;
                    font-family: "Helvetica Neue","Helvetica","Arial",sans-serif;
                    margin: 0 0 40px 0;
                }
                p{
                    margin: 5px 0;
                }
            }
        }
        .col-second-img-container{
            height: 100vh;
            .second-img-container{
                position: fixed;
                top: 0;
                right: 0;
                height: 100vh;
                width: 0;
                overflow: hidden;
                .img{
                    height: 100vh;
                    width: 100%;
                    background: #fff url("../img/fifth-section/macbook.jpg") no-repeat;
                    background-size: 50%;
                    background-position: 50%;
                    background-attachment: fixed;
                }
            }
        }
    }
}

