            .badge-line{
                height: 1px;
                width: 0%;
                background-color: #d2d2d7;
            }
            .badge-content{
                padding: 50px 0;
                opacity: 0;
                span{
                    font-size: 80px;
                    line-height: 1;
                    font-weight: 700;
                    letter-spacing: -.015em;
                    font-family: "Helvetica Neue","Helvetica","Arial",sans-serif;
                }
                p{
                    font-weight: 700;
                    color: #888;
                    font-size: 17px;
                    line-height: 1.23536;
                    font-weight: 400;
                    letter-spacing: -.022em;
                    font-family: "Helvetica Neue","Helvetica","Arial",sans-serif;
                }
            }
        }
    }
