/*Seventh section*/
.seventh-section{
    .section-graphics{
        width: 980px;
        margin: 150px auto 50px;
        font-size: 80px;
        line-height: 1.05;
        font-weight: 700;
        letter-spacing: -.015em;
        font-family: "Helvetica Neue","Helvetica","Arial",sans-serif;
        span{
            color: #acacac;
        }
    }
    .section-caption{
        color: #888;
        width: 980px;
        margin: 0 auto;
        font-size: 36px;
        line-height: 1.16667;
        font-weight: 600;
        letter-spacing: 0em;
        font-family: "Helvetica Neue","Helvetica","Arial",sans-serif;
        span{
            color: #000;
        }
    }
    .video-container{
        display: flex;
        justify-content: space-between;
        width: 980px;
        margin: 100px auto 50px;
        .left-col, 
        .right-col{
            width: calc(50% - 10px);
        }
    }
    .section-dual-tile{
        display: flex;
        justify-content: space-between;
        width: 980px;
        margin: 0 auto 50px;
        p{
            color: #888;
            width: calc(50% - 10px);
            padding: 0 200px 0 0;
            font-size: 19px;
            line-height: 1.4211;
            font-weight: 500;
            letter-spacing: .012em;
            font-family: "SF Pro Display","SF Pro Icons","Helvetica Neue","Helvetica","Arial",sans-serif;
        }
    }
}

/*Eighth section*/
.eighth-section{
    .section-graphics{
        width: 980px;
        margin: 150px auto 50px;
        font-size: 80px;
        line-height: 1.05;
        font-weight: 700;
        letter-spacing: -.015em;
        font-family: "Helvetica Neue","Helvetica","Arial",sans-serif;
        span{
            color: #acacac;
        }
    }
    .section-caption{
        color: #888;
        width: 980px;
        margin: 0 auto;
        font-size: 36px;
        line-height: 1.16667;
        font-weight: 600;
        letter-spacing: 0em;
        font-family: "Helvetica Neue","Helvetica","Arial",sans-serif;
        span{
            color: #000;
        }
    }
    .img-magic-container{
        position: relative;
        width: 1200px;
        margin: 100px auto;
        &.animated{
            img{
                &:nth-of-type(2){
                    transform: translate(0, 490px);
                }
                &:nth-of-type(3){
                    transform: translate(0, 480px);
                }
            }
        }
        img{
            transition: 1s;
            &:nth-of-type(2){
                position: absolute;
                top: 0;
                left: 0;
                transform: translate(0, 370px);
                z-index: -1;
            }
            &:nth-of-type(3){
                position: absolute;
                top: 0;
                left: 0;
                transform: translate(0, 325px);
                z-index: -2;
            }
        }
    }
}

